body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.scrollable-table {
    --bg-color: #fff;
    --header-ft-size: 12px;
    --cell-ft-size: 14px;
    --v-align: left;
    --cell-padding: 4px 22px 4px 12px;
    --border-color: #e7e9eb;
    --even-bg-color: #f9f9f9;
    --cell-height: 64px;
}

.scrollable-table * {
    box-sizing: border-box;
}
    
.scrollable-table thead th,
.scrollable-table tbody tr:not(.inline-details) td {
    padding: var(--cell-padding);
    text-align: var(--v-align);
    height: var(--cell-height);
    background-color: var(--bg-color);
}

.scrollable-table th {
    font-size: var(--header-ft-size);
    background-color: var(--bg-color);
    position: relative;
}

.scrollable-table tbody tr td,
.scrollable-table thead th {
    border-bottom: 1px solid var(--border-color);
}

.scrollable-table tbody tr:nth-child(even) td {
    background-color: var(--even-bg-color);
}

.scrollable-table tbody [tabIndex] {
    cursor: pointer;
}

.scrollable-table tbody [tabIndex]:focus {
    outline: 2px auto #47d280;
}

.scrollable-table tbody td {
    font-size: var(--cell-ft-size);
}

.scrollable-table .scrollable-table-wrapper {
    padding: 0px 20px;
}

.scrollable-table .adaptive-col-name {
    display: none;
}

.scrollable-table .scrollable-table-content {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    overflow-y: auto;
    -webkit-overflow-scrolling: auto;
    top: var(--cell-height);
}

.scrollable-table .scrollable-table-content table {
    margin-top: calc(var(--cell-height)*(-1));
    border-bottom: 2px solid var(--border-color);
    width: 100%;
}

@media all and (max-width: 640px) {
    .scrollable-table thead,
    .scrollable-table tfoot,
    .scrollable-table .scrollable-fake-table {
        display: none;
    }

    .scrollable-table tbody tr {
        display: block;
        border-color: var(--border-color);
        border-style: solid;
        border-width: 1px 1px 0px 1px;
    }
    
    .scrollable-table tbody td {
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: var(--bg-color) !important;
    }

    .scrollable-table tbody tr:not(:last-child):not(.selected-row) {
        margin-bottom: 30px;
    }

    .scrollable-table tbody td > :last-child:not(:first-child) {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
        align-items: center;
    }

    .scrollable-table tbody :not(.inline-details) td > * {
        flex: 1 1;
        text-align: right;
    }

    .scrollable-table tbody :not(.inline-details) td > .adaptive-col-name {
        display: block;
        text-align: left;
    }

    .scrollable-table .scrollable-table-content {
        top: 20px;
    }

    .scrollable-table .scrollable-table-content table {
        margin-top: 0;
        border: 0;
    }
    
    .scrollable-table .scrollable-table-wrapper {
        padding: 0 20px 0 8px;
    }

    .scrollable-table .scrollable-table-content {
        top: 8px;
    }
}
.scrollable-table .header-wrapper {
    text-align: left;
    width: 100%;
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    align-items: center;
}

.scrollable-table .header-wrapper .header-positioner {
    flex: auto;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: var(--cell-padding);
}

.scrollable-table .header-wrapper .header-positioner > div {
    overflow: hidden;
    text-overflow: ellipsis;
    display: inline-block;
}

.scrollable-table .header-wrapper .header-positioner:hover {
    width: auto;
    overflow: visible;
    background-color: #fff;
    z-index: 1;
    position: relative;
}

.scrollable-table .header-wrapper .header-positioner:hover::before {
    content: '';
    position: absolute;
    top: 0;
    left: -1px;
    right: -1px;
    bottom: 0;
    display: block;
    border: 0px solid #e7e9eb;
    border-width: 0px 1px;
    background-color: #fff;
    box-shadow: 0 1px 4px #e7e9eb;
    z-index: -1;
}
.scrollable-table .sorter,
.scrollable-table .sortable-column {
    cursor: pointer;
}

.scrollable-table .sortable-column,
.scrollable-table [class*="sorted"] {
    position: relative;
    padding-right: 30px !important;
}

.scrollable-table .sortable-column:hover,
.scrollable-table [class*="sorted"].sortable-column {
    text-decoration: underline;
}

.scrollable-table th[class*="sorted"] .sort-image {
    content: '';
    position: absolute;
    display: block;
    right: 12px;
    top: calc(50% - 2px);
    border-style: solid;
    border-width: 1px 0px 0px 1px;
    border-color: #000;
    width: 4px;
    height: 4px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    transition: -webkit-transform .3s ease-in-out;
    transition: transform .3s ease-in-out;
    transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
}

.scrollable-table th.sorted-desc .sort-image {
    -webkit-transform: rotate(-135deg);
            transform: rotate(-135deg);
}

.scrollable-table thead th .sorter:hover,
.scrollable-table thead th:not(:hover) .sorter.sorted {
    text-decoration: underline;
}
